@import "../style/global.scss";

:local(.paper) {
  line-height: 1.6;
  position: relative;
  width: 100%;
  background: white;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.18);
  &:not(:local(.noMargin)) {
    margin: 20px 0;
  }
  &:not(:local(.noPadding)) {
    padding: 12px 12px;
    @media only screen and (min-width: $screen-sm) {
      padding: 24px 28px;
    }
  }
}
